import {
  Card,
  Col,
  // Container,
  Row,
  CardBody,
  // CardImg,
  // CardHeader,
  // CardTitle,
  // FormGroup,
  // Label,
  // Button,
  // Form,
  // Input,
  // InputGroupAddon,
  // InputGroupText,
  // InputGroup,
  // Breadcrumb,
  // BreadcrumbItem,
  Spinner,
  ListGroup,
  ListGroupItem,
} from "reactstrap"
// import CIcon from '@coreui/icons-react'

import logo from '../../../src/assets/ot.png'


import { CContainer, CFade } from '@coreui/react'

// import ReactSelect from 'react-select'
// import makeAnimated from 'react-select/animated';

import conf from '../../conf.js';
import { storage } from "../../firebase/firebase";


class NosProgramme extends Component {
  constructor(props) {
      super(props)
      this.state = {
        test:[],
        ok:"",
        tournoi:[],
        page:<div style={{marginTop:"25%",marginBottom:"20%",textAlign:"center"}}>
        <Spinner type="grow" color="success" /></div>,
        status:true,
      }
  }
  
  componentDidMount() {
    const requestOptions = {
      method: 'GET',
     
    };

    fetch(conf.url+"tournoi/tournois",requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("data tournoi===>",data)
      this.setState({status:false})


      data.filter((t)=>new Date(t.dateFin)>=new Date()).map((el,idx)=>{
        el.matchs.filter((m)=>m.repousse==="PROGRAMME").map((match,idxe)=>{
          var tourn=el


          try{
                
            storage.ref("images")
            .child(tourn.multimediaProgramme.file).getDownloadURL()
            .then((url) => {
              console.log("file",url)
              tourn.multimediaProgramme=url
              
              try{
            
                storage.ref("images")
                .child(match.equipeVisites[0].multimedia.file).getDownloadURL()
                .then((url) => {
                    
                    tourn.matchs[idxe].equipeVisites[0].multimedia.file=url
                 
                    
  
                        try{
                  
                            storage.ref("images")
                            .child(match.equipeVisiteurs[0].multimedia.file).getDownloadURL()
                            .then((url) => {
                              
                              tourn.matchs[idxe].equipeVisiteurs[0].multimedia.file=url
                          
                              this.setState({tournoi:[...this.state.tournoi,tourn]})
                            
                            })
                          
                      }catch(error){
                        this.setState({tournoi:[...this.state.tournoi,tourn]})
                      }
                
                })
                
            }catch(error){
              console.log("file error")
                try{
                
                  storage.ref("images")
                  .child(match.equipeVisiteurs[0].multimedia.file).getDownloadURL()
                  .then((url) => {
                   
                    tourn.matchs[idxe].equipeVisiteurs[0].multimedia.file=url
                
                    this.setState({tournoi:[...this.state.tournoi,tourn]})
                  
                  })
                  
              }catch(error){
                this.setState({tournoi:[...this.state.tournoi,tourn]})
            }
            }
            
            
            })
          
          }catch(error){
            try{
            
              storage.ref("images")
              .child(match.equipeVisites[0].multimedia.file).getDownloadURL()
              .then((url) => {
                  
                  tourn.matchs[idxe].equipeVisites[0].multimedia.file=url
               
                  

                      try{
                
                          storage.ref("images")
                          .child(match.equipeVisiteurs[0].multimedia.file).getDownloadURL()
                          .then((url) => {
                            
                            tourn.matchs[idxe].equipeVisiteurs[0].multimedia.file=url
                        
                            this.setState({tournoi:[...this.state.tournoi,tourn]})
                          
                          })
                        
                    }catch(error){
                      this.setState({tournoi:[...this.state.tournoi,tourn]})
                    }
              
              })
              
          }catch(error){
            console.log("file error")
              try{
              
                storage.ref("images")
                .child(match.equipeVisiteurs[0].multimedia.file).getDownloadURL()
                .then((url) => {
                 
                  tourn.matchs[idxe].equipeVisiteurs[0].multimedia.file=url
              
                  this.setState({tournoi:[...this.state.tournoi,tourn]})
                
                })
                
            }catch(error){
              this.setState({tournoi:[...this.state.tournoi,tourn]})
          }
          }
           
          }



       

        
        })
      
      
       })




      // try{
      //   this.setState({tournoi:data,status:false})
 
      //  }catch(error){
      //   this.setState({tournoi:[]})
      //  }
     }
     
     );


  };

  render() {
    // const animatedComponents = makeAnimated();
    
    // var today = new Date();
    // var Annee = today.getFullYear()

  return (
    <>

   {this.state.ok}
        <CContainer fluid>
    
  {
        (!this.state.status)?

    (
    <CFade>
   

      
      <div className="mt-5"></div>

      <section id="contact" class="contact">
        <div class="container">



          {/* Tournoi inter-écoles des écoles secondaires */}

          {/* <div className="mt-5">
            <div class="section-title">
              <h2> du tournoi 'Nom du tournoi'</h2>
            </div>
          </div> */}
          
          {/* <Row className="mb-5">
            <Col>
              <strong><h3  > <i className="bx bxs-download bx-sm " style={{color:"#338C36"}}/> Télécharger le programme des matchs </h3></strong>
              <PDFDownloadLink document={<DocumentPdf/>} fileName={"ressource##"+this.state.ressource.id+".pdf"}>
                  <strong onClick={this.handleTelechargeIncrement}><h3  > <i className="bx bxs-download bx-sm " style={{color:"#338C36"}}/> Télécharger la fiche de la ressource </h3></strong>
              </PDFDownloadLink>
            </Col>
          </Row>  */}

          <Row> 
            <Card>
              
              <CardBody>
                
                {/* <CardTitle tag="h5">{element.titre}</CardTitle>
                <CardSubtitle tag="h6" className="mb-2 text-muted">bouton: {element.bouton}</CardSubtitle>
                <CardText>{element.description}</CardText> */}

              {
                  
                  <Row className="mt-4">
                  <Col> 
                    
                    <div   role="form" class="php-email-form" >
                      <h2 className="text-center mb-4">Programmes des tournois</h2>
                      <Row 
                        className="text-center mb-4" 
                        // style={{justifyContent:"center"}}
                        // style={{width:"150px",height:"150px",borderRadius:"50px",display: 'flex',  justifyContent:'center', alignItems:'center'}}
                      >
     
                      </Row>
                      
                      
                      {
                      (this.state.tournoi.length)?
                       this.state.tournoi.map((tourn)=>(
                        <>
                        <hr className="text-center mb-5"></hr>

                        <div className="form-row text-center">
                          <div class="form-group col-md-12">
                            <figcaption class="text-center mt-2 mb-2 font-weight-bold">
                            <span style={{backgroundColor:"#194D33",textAlign:'center',float:'center'}} class="badge rounded-pill text-center">{tourn.nom}</span>
                            </figcaption>
                          </div>
                          </div>
                          <div className="form-row text-center">
                         { tourn.multimediaProgramme!==""?
                           <div  class="form-group col-md-12">
                            <div >
                              <a href={tourn.multimediaProgramme} target="_blank" rel="noreferrer" style={{color:"#338C36"}} download> <i className="bx bxs-download bx-sm" /> Télécharger le programme des matchs</a>
                            </div>
                        </div>:null
                        }
                        </div>
                       { tourn.matchs.length?
                     
                        <div className="form-row text-center">
                        {tourn.matchs.map((equipe)=>( 
                           
                        <div class="form-group col-md-12">
                       
                          <ListGroup>
                            <ListGroupItem action variant="secondary" className="mb-2">
                              <Row>
                                <Col>
                                  <Row>
                                    <p className=" float-left">{equipe.equipeVisites[0].nomEquipe}</p>
                                  </Row>
                                  <Row>
                                    <img src={equipe.equipeVisites[0].multimedia?.file?equipe.equipeVisites[0].multimedia.file:logo} class="img-fluid" alt="" width="20" height="20"/>
                                  </Row>
                                </Col>
                                <Col>
                                  {/* <center> 
                                    <p style={{textAlign:"center"}}> 
                                      <strong>{equipe.scoreVisite}</strong>
                                      - 
                                      <strong> {equipe.scoreVisiteur} </strong>
                                    </p>
                                  </center> */}
                                  <center> <small style={{textAlign:"center"}}> {equipe.date}</small> </center>
                                  <center> <small style={{textAlign:"center"}}> {equipe.heure} </small> </center>
                                </Col>
                                <Col>
                                  <Row>
                                    <p className=" float-left">{equipe.equipeVisiteurs[0].nomEquipe}</p>
                                  </Row>
                                  <Row>
                                    <img src={equipe.equipeVisiteurs[0].multimedia?.file?equipe.equipeVisiteurs[0].multimedia.file:logo} class="img-fluid" alt="" width="20" height="20"/>
                                  </Row>
                                </Col>
                              </Row>
                            </ListGroupItem>
                          </ListGroup>
                         

                        </div>))}</div>:(<h5 className="text-center" style={{textAlign:"center"}}>Programme non disponible</h5>)}
                        <hr className="text-center mb-2"></hr>
                        </>
                        )):(<h4 className="text-center" style={{textAlign:"center"}}>Aucun match programmé</h4>)
                        
                      }

                        
                      </div>
                    {/* </div> */}
                  </Col>
                </Row>
              }
                

              </CardBody>
            </Card>
          </Row>

                   

        </div>
      </section>

    </CFade>):(<>{this.state.page}</>)
     }
    
    </CContainer>

    </>
  )
}
}

export default NosProgramme
